var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('Navigation'),_c('div',{style:({'margin-left': _vm.leftHeaderMargin, 'margin-bottom': '100px','width':_vm.leftHeaderWidth })},[_c('p',{staticClass:"text"},[_vm._v("Медична реформа — це важлива і водночас складна тема. Легко заплутатись у тому, звідки бере гроші лікарня, на що їх витрачає, скільки пацієнтів зазвичай обслуговує один лікар та як відрізняється прогрес медреформи по регіонах. До того ж, реформа ще не завершилась. У 2019 році закінчується перший етап — реформування первинної ланки, сімейної медицини.")]),_c('p',{staticClass:"text"},[_vm._v("Щоб зрозуміти, які зміни відбулися в медичній сфері за два роки реформи, ми створили серію візуалізацій на основі відкритих даних.")]),_c('h2',{staticClass:"text"},[_vm._v("З чого все починалось?")]),_vm._m(0),_c('p',{staticClass:"text"},[_vm._v("Дані про реформу першого рівня медичного забезпечення стали доступними завдяки співпраці НСЗУ з Державним агентством з питань електронного урядування за підтримки проекту USAID/UK aid “Прозорість та підзвітність у публічному управлінні та послугах/TAPAS”. Завдяки цим даним і візуалізаціям на їхній основі ми можемо оцінити, як змінилась медицина від початку реформи.")]),_c('h2',{staticClass:"text"},[_vm._v("Що розповідають дані про реформу?")]),_vm._m(1),_vm._m(2),_c('p',{staticClass:"text"},[_vm._v("Більшість людей звертається по допомогу до комунальних лікарень. Таких закладів найбільше, вони мають відділення навіть у невеликих селах. Але також люди підписують декларації з лікарями-підприємцями і приватними лікарнями. Тож, ми також проаналізували, скільки пацієнтів підписали декларації з лікарем у приватних клініках — і в комунальних медзакладах.")]),_c('p',{staticClass:"text"},[_vm._v("На одного лікаря в приватних клініках припадає в середньому 500 пацієнтів, тоді як у комунальних — більше тисячі. У цього може бути кілька пояснень. Перше: люди частіше звертаються в ті лікарні, в які звикли ходити до реформи. Друге: в приватних медзакладах лікарі менше залежать від грошей НСЗУ, бо паралельно надають платні послуги.")]),_vm._m(3),_c('p',{staticClass:"text"},[_vm._v("На початку реформи спеціалісти Національної Служби Здоров’я визначили, скільки пацієнтів повинно бути в одного сімейного лікаря. В середньому це 1800 людей. Якщо їх менше, лікар отримуватиме меншу оплату, якщо більше — держава за цих пацієнтів не платитиме. За роботу з пацієнтами лікар щомісяця отримує близько 60 тисяч гривень. Ця сума розрахована так, щоб лікарю після всіх витрат на оренду та додатковий персонал залишилось 30-35 тис. гривень зарплати. Як саме витрачати кошти і чому віддати пріоритет, вирішує сама лікарня — або ж лікар.")]),_c('p',{staticClass:"text"},[_vm._v("На графіках нижче можна побачити статистику кількості пацієнтів на одного лікаря і виплат на одного лікаря. Якщо лікар працює в лікарні, гроші надходять на її рахунок, а далі вже від головного лікаря залежить, як ними розпоряджатися. Якщо лікар є приватним підприємцем — він сам розпоряджається цими грошима.")]),_c('p',{staticClass:"text"},[_vm._v("Якщо ви досі ще не обрали лікаря для себе та вашої родини. Ви можете зробити це за допомогою нашої візуалізації. Ви можете знайти лікарів, що працюють у вашому місті в таблиці нижче. Та підібрати собі того хто ще не підписав максимум (дві тисячі) декларацій з пацієнтами.")]),_vm._m(4),_c('p',{staticClass:"text"},[_vm._v("Ми перевірили, що купували медичні заклади до реформи і після неї — і побачили, що лікарні почали купувати більше комп’ютерних систем, медичного обладнання, а також меблів, протипожежних систем і кондиціонерів. Найбільше зросли витрати на комп’ютерні системи.")]),_c('p',{staticClass:"text"},[_vm._v("Національна Служба Здоров’я і Міністерство Охорони Здоров’я вимагають від лікарень перевести в електронну форму всю адміністративну роботу. А отже, їм потрібно купувати обладнання і програми. Завдяки реформі паперові картки й талони відійдуть у минуле. І ми можемо побачити, що цей процес вже почався.")]),_c('p',{staticClass:"text"},[_vm._v("Також ви можете скористатися таблицею внизу, щоб самостійно перевірити, що купують лікарні. Введіть в поле пошуку назву лікарні або товару, який вас цікавить. У таблиці є 100 найбільших закупівель у кожній категорії. Категорії бувають дуже різними — наприклад, медичне обладнання, послуги з будівництва, меблі, комп’ютерна техніка та інші.")]),_c('p',{staticClass:"text"},[_vm._v("Інструмент розроблено на основі даних НСЗУ, а саме:")]),_vm._m(5),_c('p',{staticClass:"text"},[_vm._v("Дані є відкритими, тобто, це публічна інформація у форматах, які дозволяють її автоматичну обробку та використання. Ви можете отримати доступ до них через відкритий API (посилання коли буде)")])])],1)}
